<template>
  <div>
    <div v-for="(projectGroup, index) in projectGroups" :key="index">
      <ProjectsSection :projectGroup=projectGroup /> 
    </div> 
  </div> 
</template>

<script>

import siteData from '../assets/data.json'

export default {
  name: "AllProjects",

  components: {
    ProjectsSection : () => import("./ProjectsSection.vue"), 
  }, 

  data(){
    return {
      projectGroups : []
    }
  }, 

  created: function(){
    this.projectGroups.push(siteData["research"]); 
    this.projectGroups.push(siteData["data driven design"]); 
    this.projectGroups.push(siteData["design"]); 
  }
}
</script>